<template>
    <div class="cont s--signup">
        <div class="form sign-in">
            <div class="login-container">
                <h2>Log In</h2>
                <div class="login-info">
                    <span>Vindome</span> is a dynamic online wine trading platform for investors, collectors and wine enthusiasts offering rapid, direct<br/>and secure investments.
                </div>
                <float-label>
                    <input type="text" placeholder="Email" v-model='loginEmail' auto-capitalize="none" />
                </float-label>
                <v-text-field
                        v-model="loginPassword"
                        :append-icon="secureLoginPassword ? 'mdi-eye' : 'mdi-eye-off'"
                        :rules="[rules.required, rules.min]"
                        :type="secureLoginPassword ? 'password' : 'text'"
                        name="input-10-1"
                        label="Password"
                        hint="At least 8 characters"
                        counter
                        @click:append="secureLoginPassword = !secureLoginPassword"
                >
                </v-text-field>
                <p class="forgot-pass"><router-link to="/forgotpassword">Forgot password?</router-link></p>
                <v-btn class="submit login-submit" v-on:click="() => login()">Sign In</v-btn>
            </div>
        </div>
        <div class="sub-cont">
            <div class="img">
                <div class="img__text m--up">
                    <h2>New here?</h2>
                    <p>
                        Sign up and discover great amount of new opportunities!
                    </p>
                </div>
                <div class="img__text m--in">
                    <h2>One of us?</h2>
                    <p>
                        If you already has an account, just sign in. We've
                        missed you!
                    </p>
                </div>
                <div class="img__btn">
                    <span class="m--up">Create Account</span>
                    <span class="m--in">Login</span>
                </div>
            </div>
            <div class="form sign-up">
                <div class="login-container">
                    <h2>Register</h2>
                    <div class="login-info">
                        <span>Vindome</span> is a dynamic online wine trading platform for investors, collectors and wine enthusiasts offering rapid, direct<br/>and secure investments.
                    </div>
                    <float-label>
                        <input type="text" placeholder="Email" v-model='regEmail' auto-capitalize="none" />
                    </float-label>
                    <v-text-field
                            v-model="regPassword"
                            :append-icon="secureRegPassword ? 'mdi-eye' : 'mdi-eye-off'"
                            :rules="[rules.required, rules.min]"
                            :type="secureRegPassword ? 'password' : 'text'"
                            name="input-10-1"
                            label="Password"
                            hint="At least 8 characters"
                            counter
                            @click:append="secureRegPassword = !secureRegPassword"
                    >
                    </v-text-field>
                    <v-checkbox
                            v-model="checkedTerms"
                            label='I accept the Terms and Conditions'
                    ></v-checkbox>
                    <v-btn class="submit reg-submit"  v-on:click="() => registration()">Create Account</v-btn>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { required, email, minLength } from 'vuelidate/lib/validators';
    import {AAuth} from '../store/actions';
    import { mapActions } from 'vuex';

    export default {
        mounted: function() {
            document.querySelector('.img__btn').addEventListener('click', function () {
                document.querySelector('.cont').classList.toggle('s--signup');
            });
        },
        name: 'Login',
        props: {
            navigation: {
                type: Object
            }
        },
        validations: {
            loginEmail: {
                required,
                email
            },
            loginPassword: {
                required,
                minLength: minLength(8)
            },
            regEmail: {
                required,
                email
            },
            regPassword: {
                required,
                minLength: minLength(8)
            }
        },
        data() {
            return {
                loginEmail: '',
                loginPassword: '',
                regEmail: '',
                regPassword: '',
                isLoading: false,
                checkedTerms: false,
                checkedNewsletter: false,
                secureLoginPassword: true,
                secureRegPassword: true,
                rules: {
                    required: value => !!value || 'Required.',
                    min: v => v.length >= 8 || 'Min 8 characters'
                }
            };
        },
        methods: {
            login() {
                if (this.loginEmail && this.loginPassword && !this.$v.loginEmail.$invalid && !this.$v.loginPassword.$invalid) {
                    this.isLoading = true;
                    this.$store.dispatch(AAuth.GET_TOKEN, {email: this.loginEmail, pass: this.loginPassword}).then(() => {
                        this.isLoading = false;
                        this.$router.replace({ name: "tradingdesk" });
                    }).catch(err => {
                        this.isLoading = false;
                        alert('Wrong email or password')
                    })
                } else if (this.$v.email.$invalid) {
                    alert('The email you entered is not valid. Try again.')
                } else if (this.$v.password.$invalid) {
                    alert('The password you entered is not valid. Try again.')
                }
            },
            registration() {
                if (this.regEmail && this.regPassword && !this.$v.regEmail.$invalid) {
                    if (this.checkedTerms) {
                        this.isLoading = true;
                        let payload = {
                            email: this.regEmail,
                            password: this.regPassword,
                            confirmPassword: this.regPassword,
                            countryId: '255',
                            phoneNumber: '+ 1',
                            name: '',
                            enabledNotification: this.enabledNotification === true ? 1 : 0
                        }
                        this.signup(payload).then(res => {
                            if (res.statusCode === 400) {
                                this.isLoading = false;
                                alert('Email is already in use.');
                            } else {
                                this.signin({email: this.regEmail, pass: this.regPassword}).then(res => {
                                    this.isLoading = false;
                                    this.$router.replace({ name: "tradingdesk" });
                                })
                            }
                        }).catch(err => {
                            this.isLoading = false;
                            if (err.status === 400) {
                                alert('Email is already in use.')
                            } else {
                                console.log(err);
                                alert('Please try again.')
                            }
                        })
                    } else {
                        alert('You must agree with the terms of service.');
                    }
                } else {
                    alert('Please enter your credentials.');
                }
            },
            checkTOS() {
                this.checkedTerms = !this.checkedTerms;
            },
            checkNewsletter() {
                this.checkedNewsletter = !this.checkedNewsletter;
            },
            ...mapActions({
                signup: AAuth.REG_USER,
                signin: AAuth.GET_TOKEN
            })
        }
    }
</script>

<style scoped>
    *, *:before, *:after {
        box-sizing: border-box;
        margin: 0;
        padding: 0;
    }

    body {
        font-family: 'Montserrat', Arial, Helvetica, sans-serif;
        font-size: 14px;
        color: #FFF;
    }

    /*.header-container,*/
    .header-login-btn,
    .header-register-btn {
        display: none;
    }

    .login-info {
        color: #959595;
        font-size: 19px;
        margin: 35px 0 60px;
    }

    .login-info span {
        color: #024E7B;
        font-weight: 700;
    }

    input, button {
        border: none;
        outline: none;
        background: none;
        font-family: 'Open Sans', Helvetica, Arial, sans-serif;
    }

    .tip {
        font-size: 20px;
        margin: 40px auto 50px;
        text-align: center;
    }

    .login-container {
        max-width: 630px;
        margin: 100px auto;
    }

    .cont {
        overflow: hidden;
        position: relative;
        width: 100%;
        height: 100%;
    }

    .form {
        position: relative;
        width: 1280px;
        height: 100%;
        transition: -webkit-transform 1.2s ease-in-out;
        transition: transform 1.2s ease-in-out;
        transition: transform 1.2s ease-in-out, -webkit-transform 1.2s ease-in-out;
        padding: 50px 30px 0;
    }

    .sub-cont {
        overflow: hidden;
        position: absolute;
        left: 1280px;
        background: #fff;
        top: 0;
        width: 100%;
        height: 100%;
        padding-left: 640px;
        transition: -webkit-transform 1.2s ease-in-out;
        transition: transform 1.2s ease-in-out;
        transition: transform 1.2s ease-in-out, -webkit-transform 1.2s ease-in-out;
    }

    .cont.s--signup .sub-cont {
        -webkit-transform: translate3d(-1280px, 0, 0);
        transform: translate3d(-1280px, 0, 0);
    }

    button {
        display: block;
        margin: 0 auto;
        width: 260px;
        height: 36px;
        border-radius: 30px;
        color: #fff;
        font-size: 15px;
        cursor: pointer;
    }

    .img {
        overflow: hidden;
        z-index: 2;
        position: absolute;
        left: 0;
        top: 0;
        width: 640px;
        height: 100%;
        padding-top: 360px;
        background: #024E7B;
    }
    .img:before {
        content: '';
        position: absolute;
        right: 0;
        top: 50px;
        width: 640px;
        height: 120px;
        z-index: 9;
        background-image: url('../assets/login_logo.png');
        background-position: center center;
        background-repeat: no-repeat;
        transition: -webkit-transform 1.2s ease-in-out;
        transition: transform 1.2s ease-in-out;
        transition: transform 1.2s ease-in-out, -webkit-transform 1.2s ease-in-out;
    }
    .img:after {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: #024E7B;
    }
    .cont.s--signup .img:before {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }
    .img__text {
        z-index: 2;
        position: absolute;
        left: 0;
        top: 350px;
        width: 100%;
        padding: 0 20px;
        text-align: center;
        color: #FFF;
        transition: -webkit-transform 1.2s ease-in-out;
        transition: transform 1.2s ease-in-out;
        transition: transform 1.2s ease-in-out, -webkit-transform 1.2s ease-in-out;
    }
    .img__text h2 {
        margin-bottom: 10px;
        font-weight: normal;
    }
    .img__text p {
        font-size: 14px;
        line-height: 1.5;
    }
    .cont.s--signup .img__text.m--up {
        -webkit-transform: translateX(520px);
        transform: translateX(520px);
    }
    .img__text.m--in {
        -webkit-transform: translateX(-520px);
        transform: translateX(-520px);
    }
    .cont.s--signup .img__text.m--in {
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }
    .img__btn {
        overflow: hidden;
        z-index: 2;
        position: relative;
        width: 340px;
        height: 85px;
        margin: 140px auto;
        background: transparent;
        color: #FFF;
        border: 1px solid #FFF;
        text-transform: uppercase;
        cursor: pointer;
        border-radius: 5px;
        font-size: 21px;
    }
    .img__btn span {
        position: absolute;
        left: 0;
        top: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        transition: -webkit-transform 1.2s;
        transition: transform 1.2s;
        transition: transform 1.2s, -webkit-transform 1.2s;
    }
    .img__btn span.m--in {
        -webkit-transform: translateY(-72px);
        transform: translateY(-72px);
    }
    .cont.s--signup .img__btn span.m--in {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
    .cont.s--signup .img__btn span.m--up {
        -webkit-transform: translateY(72px);
        transform: translateY(72px);
    }

    h2 {
        width: 100%;
        font-size: 50px;
    }

    input {
        display: block;
        width: 100%;
        font-size: 16px;
        border-bottom: 2px solid #E0E0E0;
        height: 60px;
    }

    label.vfl-label {
        font-size: 1em;
    }

    input[type="text"] {
        margin-bottom: 40px;
    }

    .forgot-pass {
        margin-top: 15px;
        margin-bottom: 50px !important;
        text-align: right;
        font-size: 12px;
        color: #cfcfcf;
    }

    .submit {
        margin-bottom: 20px;
        background: #024E7B !important;
        color: #FFF !important;
        width: 100%;
        height: 85px !important;
        font-size: 22px;
        text-transform: uppercase;
        border-radius: 5px;
    }

    .reg-submit {
        margin-top: 80px;
    }

    .fb-btn {
        border: 2px solid #d3dae9;
        color: #8fa1c7;
    }
    .fb-btn span {
        font-weight: bold;
        color: #455a81;
    }

    .sign-in {
        transition-timing-function: ease-out;
        background: #FFF;
    }
    .cont.s--signup .sign-in {
        transition-timing-function: ease-in-out;
        transition-duration: 1.2s;
        -webkit-transform: translate3d(1280px, 0, 0);
        transform: translate3d(12800px, 0, 0);
    }

    .sign-up {
        -webkit-transform: translate3d(-900px, 0, 0);
        transform: translate3d(-900px, 0, 0);
    }
    .cont.s--signup .sign-up {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }

    .icon-link {
        position: absolute;
        left: 5px;
        bottom: 5px;
        width: 32px;
    }
    .icon-link img {
        width: 100%;
        vertical-align: top;
    }
    .icon-link--twitter {
        left: auto;
        right: 5px;
    }
</style>
